"use client";

import React from "react";
import { JobDescProps } from "@/app/utility/props";
import { useApplyJob } from "@/app/customHooks/useJobApplication";
import { useRouter } from "next/navigation";

interface JobDescFullProps extends JobDescProps {
  id: string;
}

export default function JobDesc({
  id,
  title,
  company,
  location,
  description,
}: JobDescFullProps) {
  const router = useRouter();
  const applyJobMutation = useApplyJob();

  const handleApply = async () => {
    try {
      console.log("job id is IS ID ",id)
      await applyJobMutation.mutateAsync(id);
      alert("Application submitted successfully!");
      router.push("/dashboard/my-jobs");
    } catch (error: any) {
      console.log("erorr  while applying",error)
      alert(error?.response?.data?.message || "Failed to apply. Please try again.");
    }
  };

  return (
    <div className="bg-white shadow-md rounded-xl p-6 border border-gray-100 hover:shadow-lg transition">
      <h2 className="text-2xl font-semibold text-gray-800 mb-1">{title}</h2>
      <p className="text-gray-600 font-medium">{company}</p>
      <p className="text-sm text-gray-500 mb-4">{location}</p>
      <p className="text-gray-700 text-sm mb-6 leading-relaxed">{description}</p>

      <div className="text-center">
        <button
          onClick={handleApply}
          disabled={applyJobMutation.isPending}
          className="bg-blue-600 text-white px-6 py-2 rounded-lg font-medium hover:bg-blue-700 transition shadow-sm hover:shadow-md disabled:opacity-70"
        >
          {applyJobMutation.isPending ? "Applying..." : "Apply Now"}
        </button>
      </div>
    </div>
  );
}
